@keyframes lds-double-ring {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@-webkit-keyframes lds-double-ring {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes lds-double-ring_reverse {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}
@-webkit-keyframes lds-double-ring_reverse {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}

.lds-css {
  display: flex;
    align-items: center;
    justify-content: center;
}

.lds-double-ring {
  position: relative;
}
.lds-double-ring div {
  position: absolute;
  width: 160px;
  height: 160px;
  top: 20px;
  left: 20px;
  border-radius: 50%;
  border: 8px solid #000;
  border-color: #3d7ec0 transparent #3d7ec0 transparent;
  -webkit-animation: lds-double-ring 1.5s linear infinite;
  animation: lds-double-ring 1.5s linear infinite;
}
.lds-double-ring div:nth-child(2) {
  width: 140px;
  height: 140px;
  top: 30px;
  left: 30px;
  border-color: transparent #cc4545 transparent #cc4545;
  -webkit-animation: lds-double-ring_reverse 1.5s linear infinite;
  animation: lds-double-ring_reverse 1.5s linear infinite;
}
.lds-double-ring {
  width: 77px !important;
  height: 77px !important;
  -webkit-transform: translate(-38.5px, -38.5px) scale(0.385) translate(38.5px, 38.5px);
  transform: translate(-38.5px, -38.5px) scale(0.385) translate(38.5px, 38.5px);
}
.list-item{
max-width: 50vw;
box-shadow: 3px 6px 31px -8px rgba(28,28,28,1);
box-sizing: border-box;
padding: 15px 15px 0 15px;
cursor: pointer;
margin: 40px auto;
}

.list-item__title{
    font-size: 25px;
    text-align: center;
    margin-bottom: 20px;

}
.list-item__discription{
    max-width:70%;
    font-size: 18px;
    line-height: 23px;
    margin-right: 20px;
}

.list-item__img {

margin-bottom: 40px;
width: 100%;
    
    max-width: 400px;
    }

.list-item__img img{

   width: 100%;  
    }

.list-item__place{
    padding: 20px;
background-color:#e6e5e1;
margin: 0 -15px;
}    
.list-item__place span{
color:tomato;    
    }    
.cart-item {
    width: 100%;
    box-sizing: border-box;
    padding: 40px 20px;
}

.cart-item__title {
    font-size: 30px;
    font-weight: 700;
    text-align: center;
    margin-bottom: 30px;
    line-height: 37px;


}

.cart-item__price {
    font-size: 16px;
    color: #000000;
    margin-bottom: 40px;

}

.cart-item__price span {
    color: tomato;

}

.cart-item__text {
    font-size: 20px;
    line-height: 23px;
}


.cart-item__age {
    display: block;
    font-size: 16px;
    color: #000000;
    display: flex;
    width: 100%;
    justify-content: flex-end;
    margin-bottom: 20px;

}

.cart-item__age span {
